import React from 'react';
import LogHeader from '../common/LogHeader';
import RegContent from './RegContent';
import QueueAnim from 'rc-queue-anim';


let Reg = (props) => (
    <QueueAnim animConfig={{opacity:[1, 0]}} duration="3000">
    <div key={1} className="Reg" style={{maxHeight:'53.3rem',overflow:'auto'}}>
        <LogHeader data={{name:"登录",name1:"注册",path:"/login"}}></LogHeader>
        <RegContent {...props}></RegContent>
    </div>
    </QueueAnim>
)

export default Reg