import React from 'react';
import './RegContent.css'
import './LogContent.css'
class RegContent extends React.Component{
    constructor(props){
        super();
        this.reg = this.reg.bind(this)
    }
    render(){
        return<div className="login_bg">
                <form>
                    <div className="log_tab">	
                        <div className="log_c">
                            <table border="0" style={{width:'100%', fontSsize:'1.4em'}} cellSpacing="0" cellPadding="0">
                            <tbody><tr>
                                <td style={{width:'40px'}} className="log_c_img"><img src="/image/l_1.png"/></td>
                                <td style={{textIndent:'8px',width:'65px'}}>账号：</td>
                                <td><input type="text" ref="user" defaultValue="" placeholder="" className="l_ipt"/></td>
                            </tr>
                            </tbody></table>
                        </div>
                        <div className="log_c">
                            <table border="0" style={{width:"100%", fontSize:"1.4em"}} cellSpacing="0" cellPadding="0">
                            <tbody><tr>
                                <td style={{width:'40px'}} className="log_c_img"><img src="/image/l_2.png"/></td>
                                <td style={{textIndent:'8px',width:'65px'}}>密码：</td>
                                <td><input type="password" ref="pass" defaultValue="" placeholder="" className="l_ipt"/></td>
                            </tr>
                            </tbody></table>
                        </div>
                        <div className="log_c">
                            <table border="0" style={{width:"100%", fontSize:"1.4em"}} cellSpacing="0" cellPadding="0">
                            <tbody><tr>
                                <td style={{width:'40px'}} className="log_c_img"><img src="/image/l_2.png"/></td>
                                <td style={{textIndent:'8px',width:'95px'}}>确认密码：</td>
                                <td><input type="password" ref="pass1" defaultValue="" placeholder="" className="l_ipt"/></td>
                            </tr>
                            </tbody></table>
                        </div>
                        <div className="log_c">
                            <table border="0" style={{width:"100%", fontSize:"1.4em"}} cellSpacing="0" cellPadding="0">
                            <tbody><tr>
                                <td style={{width:'40px'}} className="log_c_img"><img src="/image/l_3.png"/></td>
                                <td style={{textIndent:'8px',width:'65px'}}>邮箱：</td>
                                <td><input type="text" ref="email" defaultValue="" placeholder="" className="l_ipt"/></td>
                            </tr>
                            </tbody></table>
                        </div>
                    </div>   
                    <table border="0" style={{width:'91%', margin:'5% auto 0 auto'}} cellSpacing="0" cellPadding="0">
                    <tbody><tr height="50">
                        <td>
                            <label className="r_rad"><input type="checkbox" defaultValue=""/></label><label className="r_txt" style={{fontSize:'1.4em'}}>自动登录</label>
                            <span className="fr"><a style={{color:'#4ca6e0', fontSize:'1.3em'}}>忘记密码？</a></span>
                        </td>
                    </tr>
                    <tr height="60">
                        <td><input type="button" defaultValue="注册" onClick={this.reg} className="l_btn"/></td>
                    </tr>
                    </tbody></table>
                </form>
            </div>
        }
        reg(){
            // console.log(this.refs.user.value)
            fetch(
                'http://localhost:3000/reg',{
                    credentials:"include",
                    method:'POST',
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    body:JSON.stringify({userName:this.refs.user.value,passWord:this.refs.pass.value,userEmail:this.refs.email.value})
                }
            ).then(
              res=>res.json()
            ).then(
              data=>{
                  if(data==0){
                        alert("注册失败")
                    }else{
                        alert("注册成功");
                        this.props.history.push("/login")
                  }
                
              }
            );
        } 
}


  
export default RegContent;
